@tailwind base;
@tailwind components;
@tailwind utilities;

/* Font imports */
@font-face {
  font-family: 'Poppins';
  src: url('./assets/Fonts/Poppins/Poppins-Regular.ttf');
  font-weight: 400; 
  font-style: normal;
}
@font-face {
  font-family: 'pixel';
  src: url('./assets/Fonts/Pixelify_Sans/PixelifySans-VariableFont_wght.ttf');
  font-weight: 400; 
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: url('./assets/Fonts/Montserrat/Montserrat-VariableFont_wght.ttf');
  font-weight: 400; 
  font-style: normal;
}
.slide {
  animation: slideEffect 0.5s ease-in-out;
}

.fade-slide {
  animation: fadeSlideEffect 0.5s ease-in-out forwards;
}

@keyframes fadeSlideEffect {
  0% {
    opacity: 0;
    transform: translateX(-20px); /* Slide from left */
  }
  100% {
    opacity: 1;
    transform: translateX(0); /* Slide to original position */
  }
}

/* Add this CSS to your styles */
@keyframes slideDown {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.zoom-image {
  transition: transform 0.3s ease;
}

.zoom-image:hover {
  transform: scale(1.2); /* Adjust scale as needed */
}

.navbar {
  animation: slideDown 0.5s ease-in-out forwards; /* Add this class for animation */
}


.group:hover .rotate-y-180 {
  transform: rotateY(180deg);
}

.backface-hidden {
  backface-visibility: hidden;
}
/* Global styles */
body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  font-family:Poppins;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.content-container {
  flex: 1; /* Ensures content takes up the full remaining height */
}
.footer {
  flex-shrink: 0;
}


/* Utilities */
@layer utilities {
  /* Continuous scroll with hover pause */
  .animate-scroll {
    @apply whitespace-nowrap gap-6 overflow-hidden;
    animation: scroll 5s linear infinite;
  }

  .testimonial-card {
    @apply w-52 h-48 text-center bg-white shadow-lg rounded-lg p-6 flex flex-col items-start;
  }

  /* Define keyframes for scroll animation */
  @keyframes scroll {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(calc(-100% / 4)); /* Adjust as needed for screen width */
    }
  }

  /* Tailwind Config - Add these classes in your CSS file or use Tailwind's custom config */
@layer utilities {
  .animate-wave-slow {
      animation: moveForever 25s cubic-bezier(.55, .5, .45, .5) infinite;
      animation-delay: -2s;
      animation-duration: 7s;
  }
  .animate-wave-medium {
      animation: moveForever 25s cubic-bezier(.55, .5, .45, .5) infinite;
      animation-delay: -3s;
      animation-duration: 10s;
  }
  .animate-wave-fast {
      animation: moveForever 25s cubic-bezier(.55, .5, .45, .5) infinite;
      animation-delay: -4s;
      animation-duration: 13s;
  }
  .animate-wave-slowest {
      animation: moveForever 25s cubic-bezier(.55, .5, .45, .5) infinite;
      animation-delay: -5s;
      animation-duration: 20s;
  }

  @keyframes moveForever {
      0% {
          transform: translateX(-90px);
      }
      100% {
          transform: translateX(85px);
      }
  }
}


  /* Hover effect to pause animation */
  .hover\:animate-paused:hover {
    animation-play-state: paused;
  }
}

/* Additional pause on hover */
.hover\:animate-paused:hover .animate-scroll {
  animation-play-state: paused;
}
